.NavbarItems {
	background: linear-gradient(90deg, rgb(255, 255, 255) 0%, rgb(255, 255, 255) 100%);
	height: 80px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1.2rem;
}

.navbar-logo {
	color: rgb(0, 0, 0);
	justify-self: start;
	margin-left: 20px;
	cursor: pointer;
}

.fa-react {
	margin-left: 0.5rem;
	font-size: 1.6rem;
}

.nav-menu {
	display: grid;
	grid-template-columns: repeat(5, auto);
	grid-gap: 10px;
	list-style: none;
	text-align: center;
	width: 70vw;
	justify-content: end;
	margin-right: 2rem;
}

.nav-links {
	color:  rgba(0, 0, 0);
	text-decoration: none;
	padding: 0.5rem 1rem;
}

.nav-links:hover {
	background-color:  #000000;
	border-radius: 4px;
	transition: all 0.2s ease-out;
}

.fa-bars {
	color: #fff;
}

.nav-links-mobile {
	display: none;
}

.menu-icon {
	display: none;
}



@media screen and (max-width: 960px) {
	.NavbarItems {
		position: relative;
	}

	.nav-menu {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 500px;
		position: absolute;
		top: 80px;
		left: -100%;
		opacity: 1;
		transition: all 0.5s ease;
	}

	.nav-menu.active {
		background: #fff;
		left: 0;
		opacity: 1;
		transition: all 0.5 ease;
		z-index: 1;
	}

	.nav-links {
		text-align: center;
		padding: 2rem;
		width: 100%;
		display: table;
	}

	.nav-links:hover {
		transition: all 0.3s ease-out;
		background: #000000;
		color: #fff;
		border-radius: 0;
		transition: 250ms;
	}

	.navbar-logo {
		position:  absolute;
		top: 0;
		left: 0;
		transform: translate(-100%, 60%);
		font-size: 1.8rem;
		cursor: pointer;
	}

	.fa-times {
		color: #fff;
		font-size: 2rem;
	}

	.nav-links-mobile {
		display: block;
		text-align: center;
		padding: 1.5rem;
		margin: 2rem auto;
		border-radius: 4px;
		width: 80%;
		background: #000000;
		text-decoration: none;
		color: rgb(255, 255, 255);
		font-size: 1.5rem;
	}

	.nav-links-mobile:hover {
		background: #8b8b8b;
		color: #fff;
		transition: 250ms;
	}

	Button {
		display: none;
	}
}

