:root {
	--primary: #000000;
}

.btn {
	padding: 8px  20px;
	border-radius: 4px;
	outline: none;
	border: none;
	cursor: pointer;
}

.btn:hover {
	padding: 8px 20px;
	transition: all 0.3s ease-out;
	background: rgba(0, 0, 0);
	color: #fff;
	transition: 250ms;
}

.btn--primary  {
	background-color: var(--primary);
}

.btn--outline {
	background-color: transparent;
	color: rgba(0, 0, 0);
	padding: 8px 20px;
	border-radius: 4px;
	border: 1px solid var(--primary);
	transition: all 0.3s ease-out;
}

.btn--medium {
	padding:  8px 20px;
	border-radius: 4px;
	font-size: 18px;
	color: rgba(255, 255, 255);

}

.btn--large {
	padding:  12px 26px;
	border-radius: 4px;
	font-size: 20px;
	color: rgb(255, 255, 255);
}